<template>
  <div class="add-blog">
    <form action="" v-if="!blog.submited">
      <h1>写博客</h1>
      <label>标题</label>
      <input type="text" v-model="blog.title" required>
      <label >内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxs">
        <label for="">vue.js</label>
        <input type="checkbox" value="vue.js" v-model="blog.categories">

        <label for="">angular</label>
        <input type="checkbox" value="angular" v-model="blog.categories">

        <label for="">react.js</label>
        <input type="checkbox" value="react.js" v-model="blog.categories">

        <label for="">nodejs</label>
        <input type="checkbox" value="nodejs" v-model="blog.categories">


      </div>

      <label for="">作者</label>
      <select name="" id="" v-model="blog.author">
        <option v-for="a in authors">{{a}}</option>
      </select>
      <button v-on:click.prevent="post">提交</button>
    </form>
    <hr>
    <p>{{blog.title}}</p>
    <p>{{blog.content}}</p>
    <p></p>
    <p></p>
    <ul>
      <li v-for="c in blog.categories">{{c}}</li>
    </ul>


  </div>
</template>

<script>
  export default {
    name: 'add-blog',
    data () {
      return {
        blog:{
          title:"",
          content:"",
          categories:[],
          author:"",
          submited:false
        },
        authors:["he","she","it"]
      }
    },
    methods:{
      post:function () {
        var self = this;
        this.$http.post("https://jsonplaceholder.typicode.com/posts",{
          title:this.blog.title,
          body:this.blog.content,
          userId:2
        })
          .then(function (response) {
            console.log(response);
            self.blog.submited=true;
          })
      }
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    box-sizing: border-box;
    display: block;
  }
  .add-blog{
    padding:5%;
    max-width: 1200px;
    margin: 20px auto;
    background: ivory;
  }
  h1, h2 {
    text-align: center;
  }
  .add-blog input[type="text"]{
    padding: 8px;
    font-size:1em;
    width: 100%;
  }
  textarea{
    width: 100%;
    min-height: 200px;
  }
  label{
    margin-bottom: 20px;
    margin-top: 20px;
  }
  #checkboxs label{
    display: inline-block;
    margin: 5px;
  }
  #checkboxs input{
    display: inline-block;
    margin-right: 1em;
  }
  select{
    width: 100%;
    padding: 8px;
    font-size: 1em;
  }
  button{
    margin: 20px auto;
    padding: 8px;
    text-align: center;
    width: 80px;
    cursor: pointer;
    background: crimson;
  }


</style>



