<template>
  <div class="show-blogs">
     <div class="blog">
       <h2 v-rainbow>{{blog.title}}</h2>
       <p >{{blog.body}}</p>
     </div>

  </div>
</template>

<script>
  export default {
    name: 'single-blog',
    data () {
      return {
        blog:{},
        id:this.$route.params.id
      }
    },
    created(){
      this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id)
        .then(function (data) {
          console.log(data)
          this.blog= data.body;

        });
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .show-blogs{
    padding:5%;
    max-width: 1200px;
  }
  .blog{
    padding: 20px;
    margin: 20px auto;
    background: beige;
  }

</style>



